@import url("https://fonts.googleapis.com/css?family=Raleway|Work+Sans");
html {
  font-family: "Raleway", serif; }

body {
  padding: 0;
  margin: 0;
  min-height: 100%;
  line-height: 170%; }

h2 {
  margin: 0;
  padding: 10px;
  font-weight: lighter;
  font-size: 3em;
  line-height: 150%; }

h3 {
  font-weight: lighter; }

ul {
  list-style: none; }

#root {
  height: 100vh; }

.App {
  height: 100%; }
  .App.hogan {
    color: red; }

.Navbar {
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 110px; }

.collapseButton {
  margin: auto;
  display: none;
  height: 60px;
  width: 50px;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  fill: white; }

.DirectoryLink {
  color: black;
  max-height: 80px;
  border-right: 2px solid rgba(0, 0, 0, 0);
  font-size: 12px;
  font-family: "Work Sans", sans-serif;
  letter-spacing: 3px;
  text-align: right;
