  padding: 10px;
  border-radius: 5px;
  border-width: 1px;
  border-color: black;
  -webkit-box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2); }

.link {
  text-decoration: none; }
  .link:visited {
    text-decoration: none;
    color: blue; }

.activeLink {
  color: black; }
  .activeLink .DirectoryLink {
    border-right: 2px solid black; }
  .activeLink:visited {
    color: black; }

.nextProfileButton {
  color: black;
  max-height: 80px;
  border-right: 2px solid rgba(0, 0, 0, 0);
  font-size: 10px;
  font-family: "Work Sans", sans-serif;
  letter-spacing: 3px;
  text-align: right;
  background-color: white;
  margin: 5px;
  padding: 5px;
  cursor: pointer; }
  .nextProfileButton:hover {
    color: orange; }
  .nextProfileButton.hogan {
    background-color: red;
    color: yellow; }
    .nextProfileButton.hogan:hover {
      color: black; }

.nextProfileButtonHogan {
  background-color: red;
  font-size: 10px;
  color: yellow;
  cursor: pointer; }
  .nextProfileButtonHogan:hover {
    color: black; }

.content {
  height: 100%;
  margin: 0px 40px 0px 150px; }

.Home {
  height: 100%;
  display: grid;
  margin: auto;
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  -ms-flex-align: center;
      align-items: center; }

.profilePicture {
  display: block;
  margin: auto;
  height: 300px;
  width: 300px;
  border-radius: 300px;
  border: 1px solid black;
  background-color: lightgrey;
  -webkit-box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2); }

.homeText {
  padding: 2px; }

.About {
  max-width: 800px; }

.skillsList {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); }

.ProjectsBody {
  display: grid;
  grid-gap: 40px;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); }

.ProjectCard {
  width: 300px;
  margin: 20px 0px;
  padding: 20px 0px; }

.ProjectCardImage {
  -webkit-box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
          box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  margin: auto;
  width: 300px;
  height: 169px;
  background-repeat: no-repeat;
